@import url(https://fonts.googleapis.com/css2?family=JetBrains+Mono:wght@200&display=swap);
body {
  margin: 0;
  color: white;
  background-image: url("https://images.wallpaperscraft.com/image/waves_ripples_water_153499_1920x1080.jpg");
  background-size: cover;
  font-family: 'JetBrains Mono', monospace !important;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

i{
  font-size: 25px !important;
  margin: 0 10px 0 10px;
}

#root{
  padding: 15px 0 50px 0;
  background: #0000003b !important;
}

#dropdown-item-button{
  color: white !important;
  background: none !important;
}

::-webkit-scrollbar {
  display: none;
}

a:hover {
  text-decoration: none !important
}

.title{
    text-align: left;
    padding-left: 2rem;
}

.repo-post{
    height: auto;
    background: #00000059;
    margin: 1rem;
    padding: 15px;
    width: auto;
    overflow: hidden;
    border-radius: 10px;
    transition: 200ms;
}

.repo-post:hover{
    background: #00000082;
    transform:scale(1.1,1.1) skew(-3deg,0deg);
}

.repo-post .h4{
    font-weight: bolder;
}

.post-container{
    height: 430px;
}

.post-container a{
    color: white;
} 

.post-container a:hover{
    color: white;
}

.btn{
    background: white !important;
    color: black !important;
    border: none !important;
    border-radius: 0px !important;
}

.btn:focus{
    box-shadow: none !important;
    outline: none !important;
}

.dropdown-menu{
    width: 182px !important;
    border: 0px !important;
    border-radius: 0px !important;
    margin-top: -2px !important;
}
